import axios from "axios";
import { Message, MessageBox } from "element-ui";
import store from "../store";
import { getToken } from "@/utils/auth";
// axios
const service = axios.create({
  // baseURL: process.env.BASE_API, // apibase_url
  timeout: 15000 // 
});

// request
service.interceptors.request.use(
  config => {
    if (store.getters.token) {
      config.headers["access_token"] = getToken(); // token 
    }
    return config;
  },
  error => {
    // Do something with request error
    Promise.reject(error);
  }
);
//
// respone
service.interceptors.response.use(
  data => {
    if (!data || typeof data.data !== "object") {
      Message.error("");
    } else {
      let errmsg = "";
      const errno = data.data.errno;
      switch (errno) {
        case 1001:
          // 
          for (const i in data.data.data) {
            errmsg += data.data.data[i] + ";";
          }
          break;
        default:
          errmsg = data.data.errmsg;
          break;
      }
      if (errmsg !== "" && errno !== 0) {
        Message.error(errmsg);
      }
      if (errmsg !== "" && errno === 0) {
        Message.success(errmsg);
      }
    }
    return data.data;
  },
  error => {
    let errmsg = "";
    // debugger;
    if (error.response) {
      switch (error.response.status) {
        case 401:
          MessageBox.confirm(
            "",
            "",
            {
              confirmButtonText: "",
              cancelButtonText: "",
              type: "warning"
            }
          ).then(() => {
            store.dispatch("FedLogOut").then(() => {
              location.reload(); // vue-router bug
            });
          });
          break;
      }
    }
    Message.error(error.response.data.errmsg);
    // return Promise.reject(error.response.data.errmsg);
  }
);

export default service;



